import React, { useState } from "react";
import { db } from "../Components/Firebase";
import { FaGithub } from "react-icons/fa";
import { FaTwitter } from "react-icons/fa";
import { FaLinkedin } from "react-icons/fa";
// import gif from "./images/loadingGif.gif"

const Contact = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");
  const [loader, setloader] = useState(true);

  const handleSubmit = (e) => {
    e.preventDefault();
    setloader(true);

    db.collection("contacts")
      .add({
        name: name,
        email: email,
        message: message,
      })
      .then(() => {
        setloader(false);
        alert("Message has been submitted");
      })
      .catch((error) => {
        setloader(false);
        alert(error.message);
      });
    setName("");
    setEmail("");
    setMessage("");
  };

  return (
    <>
      <form onSubmit={handleSubmit}>
        <h1>Contact üìß </h1>
        <label>Name</label>
        <input
          type="text"
          placeholder="Name"
          value={name}
          onChange={(e) => setName(e.target.value)}
          required
        />

        <label>Email</label>
        <input
          type="text"
          placeholder="Email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          required
        />

        <label>Message</label>
        <textarea
          type="text"
          placeholder="message"
          value={message}
          onChange={(e) => setMessage(e.target.value)}
          required
        />

        <button className="details-button" type="submit">
          Submit
        </button>
      </form>

      <div className="Social">
        <ul>
          <li>
            <a
              href="https://twitter.com/kizmelvin"
              rel="noreferrer"
              target="_blank"
            >
              <FaTwitter style={{ color: "white" }} />
            </a>
          </li>
          <li>
            <a
              href="https://github.com/Kizmelvin"
              rel="noreferrer"
              target="_blank"
            >
              <FaGithub style={{ color: "white" }} />
            </a>
          </li>
          <li>
            <a
              href="https://www.linkedin.com/in/chidi-eze-5621b11bb/"
              rel="noreferrer"
              target="_blank"
            >
              <FaLinkedin style={{ color: "white" }} git />
            </a>
          </li>
        </ul>
      </div>
    </>
  );
};

export default Contact;
