import React, { Component } from "react";
import { FaAlignRight, FaWindowClose } from "react-icons/fa";
import { Link } from "react-router-dom";

export default class Header extends Component {
  state = {
    isOpen: false,
  };
  handleToggle = () => {
    this.setState({ isOpen: !this.state.isOpen });
  };
  render() {
    const navIcon = this.state.isOpen;
    return (
      <div className="nav-bar">
        <header className="nav-center">
          <div className="title">
            <Link to="/">
              <h1>Chidi Eze</h1>
            </Link>
          </div>
          <div className="nav-middle">
            <div className="menu">
              <ul
                className={
                  this.state.isOpen
                    ? "header-links show-header"
                    : "header-links"
                }
              >
                <li
                  onClick={() =>
                    this.setState({
                      isOpen: false,
                    })
                  }
                >
                  <Link to="/">Home</Link>
                </li>

                <li
                  onClick={() =>
                    this.setState({
                      isOpen: false,
                    })
                  }
                >
                  <a href="/#about">About</a>
                </li>
                <li
                  onClick={() =>
                    this.setState({
                      isOpen: false,
                    })
                  }
                >
                  <a href="/#works">Works</a>
                </li>
                <li
                  onClick={() =>
                    this.setState({
                      isOpen: false,
                    })
                  }
                >
                  <Link to="/contact">Contact</Link>
                </li>
              </ul>
            </div>
          </div>
          <div>
            {navIcon ? (
              <button
                type="button"
                className="header-btn header-icon"
                onClick={this.handleToggle}
              >
                {/* <FaWindowClose
                    className="header-icon"
                    onClick={this.handleToggle}
                  /> */}
                X
              </button>
            ) : (
              <button type="button" className="header-btn">
                <FaAlignRight
                  className="header-icon"
                  onClick={this.handleToggle}
                />
              </button>
            )}
          </div>
        </header>
      </div>
    );
  }
}
